  margin: 80px auto 0px auto; }

.review-input {
  width: 100%; }

.sign-in {
  position: relative;
  left: 4%;
  width: 92%; }

.btn {
  font-size: 14px;
  background-color: #363237;
  border-radius: 4px;
  height: 25px;
  line-height: 25px;
  margin: 10px;
  color: #e4e4e4;
  -webkit-box-shadow: 0 0 2px 2px;
          box-shadow: 0 0 2px 2px; }

.value-btn {
  border: 1px solid black;
  background-color: #363237;
  border-radius: 3px;
  margin: 5px 10px;
  line-height: 0; }

.btn-view-cars {
  width: 70%; }

.btn-sign-in {
  position: relative;
  width: 92%;
  margin-left: 4%; }

.btn-sign-up {
  position: relative;
  top: 20px;
  left: 6%;
  width: 84%; }

.btn-edit-review {
  width: 95px;
  min-width: 95px;
  margin: 5px 27%; }

.btn-reserve-car {
  line-height: 18px;
  margin: 8px 76px 3px; }

.btn-make-review {
  margin: 30px; }

.btn-cancel-reservation {
  line-height: 0; }



