.hamburger {
  position: relative;
  top: -76vh;
  z-index: 5;
  height: 150px;
  min-width: 260px;
  max-width: 500px;
  border-radius: 4px;
  border: 2px solid black;
  background-color: #69919f;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s; }

.hamburger-show {
  position: relative;
  top: -17px; }

.view-hamburger {
  right: 0; }

.hamburger-btns-container {
  display: inline-block; }

.hamburger-btn {
  font-size: 14px;
  background-color: #363237;
  border-radius: 4px;
  color: #e4e4e4;
  width: 120px;
  height: 34px;
  margin: 10px 5px;
  line-height: 34px;
  border: 1px solid black; }

.hamburger-close-btn, .hamburger-show-btn {
  top: 20px;
  width: 30px; }

.hamburger-show-btn {
  position: relative;
  top: -50px;
  left: 85%;
  -webkit-box-shadow: 0px 0px 5px 3px;
          box-shadow: 0px 0px 5px 3px; }

.hamburger-close-btn {
  font-size: 27px; }

.user-avatar {
  display: inline-block;
  width: 120px;
  height: 120px;
  margin-top: 10px;
  border-radius: 4px; }



